import {useDispatch} from 'react-redux'
import {addCharacter} from '../../store/actions/charactersAction'
import Button from './../button/Button'
import './Column.css'

export default function Column({children, title}) {
  const dispatch = useDispatch()
  
  function buttonHandler() {
    const name = prompt('–í–≤–µ–¥–∏—Ç–µ –∏–º—è –Ω–æ–≤–æ–≥–æ –ø–µ—Ä—Å–æ–Ω–∞–∂–∞')

    const newCharacter = {
      character: {id:Date.now(), name}, 
      race: title
    }

    if(name !== null){
      if(name.replaceAll(' ', '')){
        dispatch(addCharacter(newCharacter))
      }
    }
  }

  return <div className='column'>
    <div className='column-title' >
      –†–∞—Å–∞ - {title}
      <Button onClick={buttonHandler} style={{marginLeft: '25px'}}>–î–æ–±–∞–≤–∏—Ç—å</Button>
    </div>

    {children}
  </div>
}